.jumbotron {
  height: 90vh;
  width: 100%;
  position: relative;
  display: flex;
  justify-content: flex-end;
}

.jumbotron a {
  z-index: 100;
  height: 20px;
  width: 20px;
}

.jumbotron a button {
  height: 50px;
  width: 50px;
  background: transparent;
  border: none;
  cursor: pointer;
}

.overlay {
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(11, 16, 33, 1) 100%);
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 90vh;
}

.movie-description {
  display: flex;
  width: 100%;
  z-index: 100;
}

.movie-description .description-details {
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
}

.movie-description .description-details .movie-description-text {
  margin-left: 50px;
}

.movie-description .description-details p {
  font-size: 2rem;
  margin: 0;
}

.movie-description .description-details h1 {
  margin: 0;
}

.actors {
  text-align: center;
}

.actors .actor {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.actors img {
  border-radius: 5%;
  max-height: 200px;
}

@media only screen and (max-width: 600px) {
  .movie-description {
    display: flex;
    justify-content: center;
    padding: 0;
  }

  .movie-description .movie-description-text {
    text-align: center;
    margin: 0 !important;
  }

  .description-details {
    display: flex;
    flex-direction: column-reverse;
    justify-content: flex-start !important;
    align-items: center !important;
  }

  h3 {
    font-size: 1rem;
  }
}
